  width: 90% !important;
  padding: 1.1rem;
  border: none;
  outline: none;
  border-bottom-right-radius: 2rem;
  border-bottom-left-radius: 2rem;
}
.inputToSend button,
.handleWhoToHelpContainer button {
  border: none;
  background-color: transparent;
  color: white;
  padding-left: 0.5rem;
  cursor: pointer !important;
}

.handleWhoToHelpContainer input {
  padding: 0.5rem;
  border-radius: 0;
}
.inputToSend button:hover,
.handleWhoToHelpContainer button:hover {
  font-size: 1rem;
  transition: 0.2s;
}

